.network__item {
  margin-right: 10px;
  @media screen and (min-width: 1280px) {
    margin-bottom: 20px;
  }
}

.network__link {
  display: flex;
  justify-content: start;
  align-items: center;
  @media screen and (min-width: 1280px) {
    align-items: center;
  }
}

.link__icon {
  width: 25px;
  height: 25px;
  fill: var(--color-main-text);
  @media screen and (min-width: 768px) {
    width: 40px;
    height: 40px;
    margin-right: 20px;
  }
}
.link__name {
  margin-left: 10px;
}

.network__name {
  overflow: hidden;
  @media screen and (min-width: 1280px) {
    overflow: 1;
    font-size: 22px;
    color: var(--color-diagram-need);
  }
}
